body {
  background-color: #222;
  color: white;
}
.app {
  text-align: center;
}

.task_list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.task {
  padding: 20px;
  margin: 1px;
  width: 25%;
  @media (max-width: 1024px) {
    width: 75%;
  }
  background-color: white;
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  &.finished {
    text-decoration: line-through;
    background-color: darken(white, 20%);
  }
  button {
    font-size: 10px;
    width: 25px;
    height: 25px;
    color: white;
    padding: 5px;
    border: none;
    border-radius: 50%;
    background-color: white;
    &.add {
      background-color: transparent;
      border: 2px solid rgba(0, 0, 0, 0.438);
      &.finished {
        background-color: green;
        border-color: black;
      }
    }
    &.remove {
      background-color: red;
    }
  }
}
form {
  p {
    font-size: 25px;
    font-weight: bold;
    text-transform: uppercase;
    font-family: poppins;
  }
  input {
    padding: 10px;
    text-align: center;
  }
  button {
    margin: 10px;
    padding: 10px;
    border: none;
    color: white;
  }
  button[type="submit"] {
    background-color: green;
  }
  button[type="remove"] {
    background-color: crimson;
  }
  input[type="text"] {
    background-color: transparent;
    border: none;
    border-bottom: 3px solid royalblue;
    color: white;
    &:focus {
      outline: none;
      background-color: rgba(255, 255, 255, 0.05);
      box-shadow: 0 0 10px 5px rgba(255, 255, 255, 0.05);
    }
  }
  input[type="date"] {
    background-color: transparent;
    border: 3px solid royalblue;
    color: white;
    &:focus {
      outline: none;
    }
  }
}

.remove-finished {
  padding: 10px;
  background-color: crimson;
  color: white;
  border: none;
}

::-webkit-calendar-picker-indicator {
  filter: invert(1);
}
